.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 20vh;
  background-color:  #63ace5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  min-height: 65vh;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  width: 100%;
  background-color: #e7eff6;
  justify-content: center
}


.App-footer {
  min-height: 15vh;
  background-color:  #63ace5;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.select-region{

}

.logos{
  display: flex;
  justify-content: center;
}

.deWaag{
  height: 8vh;
}

.dropdown-menu{
  max-height: 200px;
  overflow-y: scroll;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media only screen and (max-width: 1026px) {
  .App-header h1{
    font-size: larger;
  }
  .App-footer h2{
    font-size: large;
  }
}
