.step1Container{
  display: flex;
  flex-direction: column;
}

.step1Button{
  padding-top: 20px;
}

.dropdown-menu{
  width:100%;
}



.step2Container{
  display: flex;
  flex-direction: column;
}

.startButton{
  padding-top: 20px;
}

.question-area{
  flex: 2;
  width: 50vw;
}

.KieswijzerButtons{
  padding-top: 10vh;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
}

.KieswijzerButton{
  padding: 10px;
  font-size: 24px;
  border-radius: 10px;
  display: flex;
  flex: 1;
}

.top3container{
  padding-top: 10vh;
}

.top3{
  display: flex;
}

.stepContainer{
  display: flex;
  width: 100vw;
  flex-wrap : wrap;
}


.placeholder{
  flex: 1;
}

.category{
  flex: 1;
  margin-left: 40px;
}

.inner-category{
  border: 1px solid grey;
  height: 100%;
  width: 90%;
  float: left;
}

.category p{
  padding-top: 10px;
  font-size: 16px;
  padding-left: 7px;
  padding-right: 7px;
}

.question{
  height: 10vh;
}


@media only screen and (max-width: 1026px) {
    .category {
        display: none;
    }
    .placeholder {
      display: none;
    }
    .top3container{
      padding-top: 2vh;
    }
}
